export default {
	data() {
		return {
			token: '',
			userInfo: '',
			avatarUrl: '',
			nickName: '',
			marketid:'',
			redCount:''
		}
	},
	onLoad: function() {
		this.marketid = uni.getStorageSync('marketid')
	},
	onShow: function() {
		uni.getStorage({
			key: 'token',
			success: (res) => {
				this.token = res.data
				uni.getSetting({
					success: (res) => {
						console.log(res)
						if (res.authSetting['scope.userInfo']) {
							// 已经授权，可以直接调用 getUserInfo 获取头像昵称
							uni.getUserInfo({
								success: (res) => {
									console.log(res)
									this.avatarUrl = res.userInfo.avatarUrl,
										this.nickName = res.userInfo.nickName
									this.getUser();
									this.getRedList()
								}
							})
						} else {
							this.getUser();
							
						}
					}
				})
			},
			fail: (error) => {
				this.token = ''
			}
		})
		
	},
	methods: {
		
		//获取红包列表
		getRedList:function(){
			var data = {
				token:this.token,
				businessId:this.marketid,
				pageIndex:1,
				pagec:1
			}
			this.$urlRequest('getUserRed.do',data).then(res=>{
				this.redCount = res.data[0].count
				
			})
		},
		
		getUser: function() {
			this.$urlRequest('getUserInfo.do', {
				token: this.token
			}).then(res => {
				this.userInfo = res.data
				uni.setStorage({
					key: 'phone',
					data: res.data.mbphone
				})
				this.getRedList()
			})
		},
		//退出登陆
		logout: function() {
			uni.removeStorage({
				key: 'token',
				success: (res) => {
					this.token = ''
				}
			})
			uni.removeTabBarBadge({
				index:2
			})
		},
		//登陆
		goToLogin: function() {
			uni.getSetting({
				success(res) {
					uni.navigateTo({
						url: '/pages/login/login_type',
					})
					// if (res.authSetting['scope.userInfo']) {
					// 	// 已经授权，可以直接调用 getUserInfo 获取头像昵称
					// 	uni.navigateTo({
					// 		url: '/pages/login/login_type',
					// 	})
					// } else {
					// 	uni.navigateTo({
					// 		url: '/pages/getUserInfo/getUserInfo',
					// 	})
					// }
				}
			})
		},
		//前往我的订单
		gotoMyorder:function(type){				
			if(!!!this.token){
				this.goToLogin()
			}else{					
				uni.navigateTo({
					url:'/pages/order/order?type=' + type
				})
			}
			
		},
		goTo:function(e){
			if(!!!this.token){
				this.goToLogin()
			}else{					
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url:'/pages/' + url + '/' + url
				})
			}	
		},
		//前往编辑资料
		editInfo:function(){
			uni.navigateTo({
				url:'/pages/editInfo/editInfo'
			})
		},
		gotoEwm:function(){
			uni.navigateTo({
				url:'/pages/index/ceshi'
			})
		}
	}
}
